import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-third',
  templateUrl: './third.component.html',
  styleUrls: ['./third.component.css']
})
export class ThirdComponent implements OnInit {

  

  sum:number;
  constructor() { }

  ngOnInit() {
  }
 add(v1:number,v2:number)
 {
    this.sum=(+v1)+(+v2);
 }
 sub(v1:number,v2:number)
 {
    this.sum=(+v1)-(+v2);
 }
 mul(v1:number,v2:number)
 {
    this.sum=(+v1)*(+v2);
 }
 div(v1:number,v2:number)
 {
    this.sum=(+v1)/(+v2);
 }
}
