import { Component, OnInit } from '@angular/core';
import{HttpClient} from'@angular/common/http';
@Component({
  selector: 'app-test1',
  templateUrl: './test1.component.html',
  styleUrls: ['./test1.component.css']
})
export class Test1Component implements OnInit {

  eno:number;
  ename:string;
  age:number;
  show:boolean=false;
  currentobj:any;
  constructor(private http:HttpClient) { }
data:any[];
  ngOnInit() {
    this.http.get<any[]>('/test1').subscribe(data=>this.data=data);
  }

  deleteRecord(v)
  {
    console.log(v);
    this.http.delete<any[]>('/test1'+"/"+v._id).subscribe(data=>this.data=data);
  }

editRecord(i)
{
  this.currentobj=i;
  this.show=true;
  this.eno=i.eno;
  this.ename=i.ename;
  this.age=i.age;
}


updateAndSaveRecord(v)
{
  v._id=this.currentobj._id;
  console.log(v);
  console.log(this.currentobj._id);
  this.http.put<any[]>('/test1',v).subscribe(data=>this.data=data);
  this.show=false;
}
}
